import React, { useState } from "react";
import {
  Tab,
  Tabs,
  Grid,
  Cell,
  CardTitle,
  CardText,
  Button,
  CardActions,
  Card,
} from "react-mdl";

const Projects = () => {
  const [state, setState] = useState({
    activeTab: 2,
  });
  const myFunction = () => {
    if (state.activeTab === 0) {
      return (
        <div className="bg3">
          <Card
            shadow={0}
            style={{ width: "320px", height: "320px", margin: "auto" }}
          >
            <CardTitle
              expand
              style={{
                color: "black",
                background: "url(cd4.png)  no-repeat #46B6AC",
              }}
            >
              Covid 19 Tracker
            </CardTitle>
            <CardText>
              Information of Live count of Covid-19 Cases in Whole world or
              Respective Country.
            </CardText>
            <CardActions border>
              <Button colored>View Updates</Button>
            </CardActions>
          </Card>
        </div>
      );
    }
  };
  const newFunction = () => {
    if (state.activeTab === 1) {
      return (
        <div className="bg2">
          <h1>This Project is Under Development</h1>
        </div>
      );
    } else if (state.activeTab === 2) {
      return (
        <div>
          <Grid className="bg">
            <h1>This Project is Under Development</h1>
          </Grid>
        </div>
      );
    }
  };

  return (
    <div>
      <Tabs
        activeTab={state.activeTab}
        onChange={(tabID) => {
          setState({ ...state, activeTab: tabID });
        }}
        ripple
      >
        <Tab>Covid 19 Tracker</Tab>

        <Tab>Hotel Website</Tab>
        <Tab>Weather Report App</Tab>
      </Tabs>
      <section>
        <Grid>
          <Cell>{myFunction()}</Cell>
          <Cell>{newFunction()}</Cell>
        </Grid>
      </section>
    </div>
  );
};

export default Projects;
