import React from "react";
import "./App.css";
import Home from "./components/Home";

import Projects from "./components/Projects";
import Contact from "./components/Contact";
import Resume from "./components/Resume";
import { Layout, Header, Navigation, Drawer, Content } from "react-mdl";
import { BrowserRouter, Link, Switch, Route } from "react-router-dom";
function App() {
  return (
    <div>
      <BrowserRouter>
        <div className="demo-big-content">
          <Layout>
            <Header title="Siddhesh Gaykar" className="headerApp" scroll>
              <Navigation>
                <Link to="/">Home</Link>

                <Link to="/projects">Projects</Link>
                <Link to="/resume">Resume</Link>
                <Link to="/contact">Contacts</Link>
              </Navigation>
            </Header>
            <Drawer
              title="Siddhesh Gaykar"
              style={{ color: "orange", backgroundColor: "black" }}
            >
              <Navigation className="headerapp2">
                <Link style={{ color: "darkorange" }} to="/">
                  Home
                </Link>
                <Link style={{ color: "orange" }} to="/projects">
                  Projects
                </Link>
                <Link style={{ color: "orange" }} to="/resume">
                  Resume
                </Link>
                <Link style={{ color: "orange" }} to="/contact">
                  Contacts
                </Link>
              </Navigation>
            </Drawer>
            <Content>
              <div className="page-content" />
              <Switch>
                <Route exact path="/" component={Home}></Route>
                <Route exact path="/projects" component={Projects}></Route>
                <Route exact path="/contact" component={Contact}></Route>
                <Route exact path="/resume" component={Resume}></Route>
              </Switch>
            </Content>
          </Layout>
        </div>
      </BrowserRouter>
    </div>
  );
}

export default App;
